<template>
	<div
		:class="[
			'field-wrapper',
			'dropdown',
			{
				open,
			},
		]"
	>
		<fieldset ref="dropdownWrapper">
			<button
				ref="dropdownToggle"
				@click.stop.prevent="toggle"
				:class="[
					'dropdown-toggle',
					'dc-button',
					'dc-button--has-title',
					'dc-button--has-icon',
					{
						open: open,
					},
				]"
				:aria-controls="dropdownId"
				:aria-expanded="open"
			>
				<legend :for="name">{{ legendText }}</legend>
				<span class="menu-dots">
					<span class="dot top"></span>
					<span class="dot middle"></span>
					<span class="dot bottom"></span>
				</span>
			</button>
			<div
				ref="dropdownOptions"
				:id="dropdownId"
				:class="[
					'dropdown-wrapper',
					{
						open: open,
					},
				]"
				:aria-hidden="!open"
			>
				<div>
					<input :id="name + '-option-all'" type="checkbox" :value="null" v-on:click="reset()" />
					<label
						:for="name + '-option-all'"
						:class="[
							{
								selected: !checkedValues.length,
							},
						]"
						>All</label
					>
				</div>

				<div v-for="(option, index) in options" :key="name + '-option-' + index">
					<input
						:id="name + '-option-' + index"
						type="checkbox"
						:name="name"
						:value="option.value"
						v-on:click="choose(option.value, option.label)"
					/>
					<label
						:for="name + '-option-' + index"
						:class="[
							{
								selected: checkedValues.includes(option.value),
							},
						]"
						>{{ option.label }}</label
					>
				</div>
			</div>
		</fieldset>
	</div>
</template>

<script>
export default {
	props: {
		name: {
			type: String,
			required: true,
		},
		label: {
			type: String,
			required: true,
		},
		options: {
			type: Array,
			required: true,
		},
		open: {
			type: Boolean,
			required: true,
		},
		initialSelection: {
			type: String,
			required: false,
		},
		multiselect: {
			type: Boolean,
			required: true,
		},
	},
	data() {
		return {
			checkedValues: [],
			legendText: '',
			usesMultiselect: true,
		};
	},
	computed: {
		dropdownId() {
			return 'dropdown-' + this.name;
		},
	},
	methods: {
		toggle() {
			this.$emit('toggle-filter', this.name, this.dropdownId);
		},
		choose(newValue, newLabel) {
			this.$emit('update-filter', this.name, newValue, this.usesMultiselect);
			if (this.usesMultiselect) {
				if (this.checkedValues.includes(newValue)) {
					const index = this.checkedValues.indexOf(newValue);
					if (index > -1) {
						this.checkedValues.splice(index, 1);
					}
				} else {
					this.checkedValues.push(newValue);
				}
			} else {
				this.legendText = newLabel;
				this.checkedValues = [newValue];
				this.toggle();
			}
		},
		reset() {
			this.$emit('update-filter', this.name, null, this.usesMultiselect);
			this.legendText = this.label;
			this.checkedValues = [];
		},
	},
	mounted() {
		this.usesMultiselect = this.multiselect !== '0';
		if (!this.usesMultiselect) {
			for (let i = 0; i < this.options.length; i++) {
				if (this.options[i].value !== this.initialSelection) continue;
				this.legendText = this.options[i].label;
			}
		}
		if (this.legendText === '') {
			this.legendText = this.label;
		}
	},
};
</script>
