<template>
	<div ref="filtersWrapper" class="dc-post-grid-filters-wrapper">

        <div :class="['dc-post-grid-search-wrapper']" v-if="searchFormPlaceholder">
            <Search 
            	:model="form.s"
            	:searchFormPlaceholder="searchFormPlaceholder" 
            	v-on:updateSearchTerm="updateSearchTerm" />
        </div>

        <div v-if="filters" :class="['dc-post-grid-dropdowns-wrapper']">
        	<h3 v-if="filterLabel">{{ filterLabel }}</h3>
            <div v-for="filter in filters" :key="'filter-' + filter.name">
                <Dropdown 
                    v-if="filter.type === 'default'" 
                    :label="filter.label" 
                    :name="filter.name" 
                    :options="filter.options" 
                    :multiselect="filter.multiselect || true" 
                    :initialSelection="filter.initial_selection"
                    :open="(filter.name===openFilter)"
                    v-on:toggle-filter="toggleFilter" 
                    v-on:update-filter="updateAndSearch" />
            </div>
		</div>

		<input 
			v-on:click.stop.prevent="search" 
			:id="searchButtonId" 
			type="submit" 
			tabindex="-1" 
			:class="['screen-reader-text', { loading: loading }]" 
			:value="Search">

	</div>
</template>

<script>
import Search from '@dc-post-grid/layout/Search.vue';
import Dropdown from '@dc-post-grid/layout/filters/Dropdown';
import { Searchable } from '@dc-post-grid/mixins/Searchable';
export default {
	mixins: [ Searchable ],
	data() {
		return {
			openFilter: null,
			minHeight: 0
		}
	},
    props: {
        searchFormPlaceholder: {
            type: String,
            required: false,
            default: '',
        },
        filterLabel: {
            type: String,
            required: false,
            default: '',
        }
    },
	methods: {
		updateFilter(name, newValue, usesMultiselect) {
			this.$emit('update-filter', name, newValue)
		},
		toggleFilter(name, targetDropdownId) {
			this.openFilter = ( this.openFilter===name ) ? null : name
			if (this.openFilter===null) {
				this.$emit('update-post-grid-min-height', 0);
			} else {
				window.setTimeout(() => {
					const dropdownOptions = document.getElementById(targetDropdownId)
					this.$emit('update-post-grid-min-height', dropdownOptions.clientHeight);
				}, 500);
			}
		}
	},
	components: {
		Dropdown,
        Search,
	}
}
</script>
