<template>
	<div
		ref="searchWrapper"
		:class="['dc-post-grid-input-wrapper', { focused: focused }]"
		v-on:click="focusSearch()"
	>
		<input
			name="dc-post-grid-search"
			type="search"
			:placeholder="searchFormPlaceholder"
			:id="dc - post - grid - search - input"
			v-model="model"
			:class="['dc-post-grid-search-input']"
			ref="searchInput"
			@focus="focused = true"
			@blur="focused = false"
		/>
	</div>
</template>

<script>
export default {
	props: {
		model: {
			type: Object,
			required: true,
		},
		searchFormPlaceholder: {
			type: String,
			default: '',
			required: true,
		},
	},
	data() {
		return {
			focused: false,
		};
	},
	watch: {
		model(newVal) {
			this.$emit('updateSearchTerm', newVal);
		},
	},
	methods: {
		focusSearch() {
			this.$refs.searchInput.focus();
		},
	},
};
</script>
