import DcPostGrid from '../layout/DcPostGrid';

const dcPostGrids = document.querySelectorAll('.dc-post-grid-component-wrapper');
if (dcPostGrids.length) {
	for (let i = 0; i < dcPostGrids.length; i++) {
		const dcPostGridId = dcPostGrids[i].id;
		const dcPostGridBlockId = dcPostGridId.replace('dc_post_grid_', '');
		const dcPostGridData = dcpgData[dcPostGridBlockId];
		console.log(dcPostGridData);
		new Vue({
			render: (h) =>
				h(DcPostGrid, {
					props: dcPostGridData,
				}),
		}).$mount('#' + dcPostGridId);
	}
}
