export const Searchable = {
	props: {
		filters: {
			type: Array,
			required: true,
		},
		lang: {
			type: String,
			required: false,
		},
		s: {
			type: String,
			required: false,
		},
		searchButtonId: {
			type: String,
			required: true,
		},
		initialModel: {
			type: Object,
			required: false,
		},
		currentPaged: {
			type: Number,
			required: true,
		},
		gridSettings: {
			type: Array,
			required: true,
		},
	},
	data() {
		return {
			timeout: null,
			needsRefresh: false,
			previousTerm: '',
			initialPaged: null,
			previousPaged: '2',
			previousForm: {},
			form: {
				s: null,
				paged: 2,
				category: [],
			},
		};
	},
	computed: {
		isQueryable() {
			return (
				typeof this.gridSettings !== 'undefined' &&
				typeof this.gridSettings.queryable !== 'undefined' &&
				this.gridSettings.queryable
			);
		},
	},
	methods: {
		toggleTextSearchFocused() {
			this.textSearchFocused = !this.textSearchFocused;
		},
		doTextSearch() {
			clearTimeout(this.timeout);
			this.timeout = setTimeout(() => {
				this.refreshQuery();
				this.search();
			}, 250);
		},
		updateSearchTerm(newVal) {
			if (typeof newVal === 'undefined' || newVal === '') {
				this.form.s = null;
			} else {
				this.form.s = newVal;
			}
			// If the newVal is different than oldVal, and newVal is !empty
			if (typeof newVal !== 'undefined' && newVal !== this.previousTerm) {
				this.doTextSearch();
			}
		},
		updateAndSearch(name, value, usesMultiselect) {
			if (this.previousForm[name] !== value) {
				this.refreshQuery();
				this.previousForm[name] = [value];
				if (typeof this.form[name] === 'undefined') {
					this.form[name] = [];
				}
				if (usesMultiselect) {
					if (this.form[name].includes(value)) {
						const index = this.form[name].indexOf(value);
						if (index > -1) {
							this.form[name].splice(index, 1);
						}
					} else {
						this.form[name].push(value);
					}
				} else {
					this.form[name] = [value];
				}
				this.search();
			}
		},
		refreshQuery() {
			this.form.paged = 0;
			this.$emit('update-current-paged', this.initialPaged);
			this.previousYear = this.form.year;
			if (typeof this.filters !== 'undefined') {
				for (var i = 0; i < this.filters.length; i++) {
					this.previousForm[this.filters[i].name] = this.form[this.filters[i].name];
				}
			}
			this.previousTerm = this.form.s;
		},
		serialize(obj) {
			let str =
				'?' +
				Object.keys(obj)
					.reduce(function (a, k) {
						a.push(k + '=' + encodeURIComponent(obj[k]));
						return a;
					}, [])
					.join('&');
			return str;
		},
		updateDefaultFilterValuesIfNoSelections(formValues) {
			return formValues;
			/*
			This logic is disabled. Replaced with __not_in hidden filters for terms.
			if (typeof this.filters === 'undefined' || this.filters.length === 0) return formValues;
			this.filters.forEach((filter, filterIndex) => {
				if (formValues[filter.name].length===0) {
					let values = [];
					filter.options.forEach((option, optionIndex) => {
						values.push(option.value)
					})
					formValues[filter.name] = values
				}
			})
			return formValues
			*/
		},
		getUrlFriendlyKey(key) {
			if (key === 'post_type') return 'post_types';
			if (key === 'paged') return 'dc_page';
			return key;
		},
		updateUrlSearchHistory(formValues) {
			let queryString = '';
			for (const [key, value] of Object.entries(formValues)) {
				const paramKey = this.getUrlFriendlyKey(key);
				if (
					(paramKey === 'dc_page' && ['0', 0].includes(value)) ||
					value.length === 0 ||
					['posts_per_page', 'order', 'orderby', 'total_pages', 'post_types'].includes(paramKey)
				)
					continue;
				queryString += queryString === '' ? '?' : '&';

				queryString += paramKey + '=';
				if (typeof value === 'string' || typeof value === 'number') {
					queryString += value;
				} else {
					if (typeof value === 'object') {
						value = Object.values(value);
					}
					queryString += value.join(',');
				}
			}
			history.replaceState(null, '', queryString);
		},
		search() {
			// if (typeof this.form.s === 'undefined') return
			this.$emit('searching');
			let formValues = this.form;
			if (formValues['s'] === null || typeof formValues['s'] === 'undefined') {
				delete formValues['s'];
			}
			const formValuesFinal = this.updateDefaultFilterValuesIfNoSelections({ ...formValues });
			axios.post(window.dcpgData.searchEndpoint + this.serialize(formValuesFinal)).then((response) => {
				if (response.data.success) {
					if (this.isQueryable) {
						this.updateUrlSearchHistory(formValuesFinal);
					}
					this.form.paged = response.data.paged;
					this.needsRefresh = this.form.paged <= this.previousPaged;
					const resultData = {
						paged: response.data.paged,
						posts: response.data.posts,
						endOfResults: response.data.endOfResults,
						needsRefresh: this.needsRefresh,
					};
					if (typeof response.data.totalPages !== 'undefined') {
						this.$emit('update-total-pages', response.data.totalPages);
					}
					this.$emit('results', resultData);
					this.previousPaged = this.form.paged;
				} else {
					this.$emit('error');
				}
			});
		},
		setInitialModelsFromFilters() {
			if (typeof this.filters !== 'undefined' && this.filters !== null) {
				for (var i = 0; i < this.filters.length; i++) {
					const value = this.filters[i].value[0] === '' ? [] : this.filters[i].value;
					this.form[this.filters[i].name] = value;
					this.previousForm[this.filters[i].name] = value;
				}
			}
		},
		initSearchModel() {
			//this.form.s = this.$route.params.s
			this.initialPaged = this.currentPaged;
			this.setInitialModelsFromFilters();
			if (typeof this.lang !== 'undefined') {
				this.form.lang = this.lang;
			}
			if (typeof this.s !== 'undefined') {
				this.form.s = this.s;
			}
			if (typeof this.initialModel !== 'undefined') {
				for (const [key, value] of Object.entries(this.initialModel)) {
					this.form[key] = value;
				}
			}
		},
	},
	mounted() {
		this.initSearchModel();
	},
	watch: {
		currentPaged(newVal) {
			this.form.paged = newVal;
			this.search();
		},
	},
};
