<template>
	<button
		:class="['dc-post-grid-load-more', 'dc-button', { loading }]"
		@click.stop.prevent="loadMore(currentPaged)"
		>{{ label }}</button
	>
</template>

<script>
import { Translatable } from '@dc-post-grid/mixins/Translatable';
export default {
	mixins: [Translatable],
	props: {
		loading: {
			type: Boolean,
			required: true,
		},
		searchButtonId: {
			type: String,
			required: true,
		},
		currentPaged: {
			type: Number,
			required: true,
		},
		totalPages: {
			type: Number,
			required: true,
		},
		totalPosts: {
			type: Number,
			required: true,
		},
	},
	data: {
		totalVisiblePosts: null,
	},
	computed: {
		label() {
			return this.loading ? this.dctxt('loading') : this.dctxt('loadMore');
		},
	},
	methods: {
		loadMore(currentPaged) {
			this.currentPaged = currentPaged + 1;
			this.$emit('update-current-paged', this.currentPaged);
		},
		countVisiblePosts() {
			return this.$parent.$refs['resultsWrapperInnerAlt'].length;
		},
	},
	watch: {
		loading() {
			this.totalVisiblePosts = this.countVisiblePosts();
		},
	},
	created() {
		this.totalVisiblePosts = this.countVisiblePosts();
	},
};
</script>
