<template>
	<article
		:id="id"
		:class="[labelClass, 'dc-post-grid-card default-card post-summary default']"
		v-on:click.stop.prevent="goToPost()"
	>
		<div
			:class="[
				'default-card__image',
				{
					'no-image': !image,
				},
			]"
		>
			<img v-if="image" :src="image.src" :srcset="image.srcset" :alt="image.alt" />
		</div>
		<div ref="cardContentWrapper" class="default-card__content">
			<div class="default-card__content__inner">
				<span class="post-date" v-if="date">{{ date }}</span>
				<h3 v-if="title" class="card-title">{{ title }}</h3>
				<p v-if="excerpt" class="card-excerpt">{{ excerpt }}</p>
			</div>
			<a
				ref="postLink"
				v-if="url"
				:href="url"
				class="dc-button dc-button--has-icon dc-button--color-orange"
				:aria-label="`Read article: ${title}`"
			>
				<span class="icon icon-arrow-right"></span>
			</a>
		</div>
	</article>
</template>

<script>
export default {
	props: {
		id: {
			type: String | Number,
			required: true,
		},
		title: {
			type: String,
			required: true,
		},
		url: {
			type: String,
			required: true,
		},
		excerpt: {
			type: String,
			required: false,
		},
		label: {
			type: Object,
			required: false,
		},
		image: {
			type: Object,
			required: false,
		},
		date: {
			type: String,
			required: false,
		},
	},
	computed: {
		labelClass() {
			if (typeof this.label === 'undefined' || typeof this.label.class === 'undefined') return '';
			return this.label.class;
		},
	},
	methods: {
		goToPost() {
			window.location.href = this.url;
		},
	},
};
</script>
