<template>
	<div class="dc-post-grid-pagination">
		<span class="dc-post-grid-current-page">{{ currentPaged }} / {{ totalPages }}</span>
		<button 
	        :class="['dc-post-grid-previous', 'dc-button', {
	        	disabled: (currentPaged<=1)
	        }]"
	        :aria-disabled="currentPaged<=1"
			@click.stop.prevent="changePage('previous')">
				<span class="dc-button-text">{{ dctxt('previous') }}</span>
		</button>
		<button 
	        :class="['dc-post-grid-next', 'dc-button', {
	        	disabled: (currentPaged>=totalPages)
	        }]"
	        :aria-disabled="currentPaged>=totalPages"
			@click.stop.prevent="changePage('next')">
				<span class="dc-button-text">{{ dctxt('next') }}</span>
		</button>
	</div>
</template>

<script>
import { Translatable } from '@dc-post-grid/mixins/Translatable';
export default {
	mixins: [
		Translatable
	],
	props: {
		currentPaged: {
			type: Number,
			required: true
		},
		totalPages: {
			type: Number,
			required: true
		}
	},
	methods: {
		changePage(direction) {
			const newCurrentPage = (direction==='previous') ? this.currentPaged - 1 : this.currentPaged + 1;
			if ( newCurrentPage < 1 || newCurrentPage > this.totalPages ) return;
            this.$emit('update-current-paged', newCurrentPage)
		}
	}
}
</script>
